
import common from '@/libs/common.mixin'
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default{
mixins: [common],
components:{
Cell,
Card,
Group,
Popup,
Flexbox,
FlexboxItem
},
props: {
nameField: {
type: String,
default: 'student_name'
},
urlField: {
type: String,
default: 'photo_url'
},
list: {
type: Array,
default: ()=>{
return []
}
}
},
data () {
return {
popupShow: false
}
},
computed: {
style () {
let headerHeight = this.headerHeight

return 'width:100%;height:100%;top:'+headerHeight+'px;background:#fff'
}
},
methods: {
show () {
this.popupShow = true
},
close () {
this.popupShow = false
}
}
}




