import Vue from 'vue'
import moment from 'moment'
import {_} from '@/libs/util'
import common from '@/libs/common.mixin'
import wxjssdk from '@/libs/wxjssdk.mixin'
import globals from '@/libs/globals.mixin'
import SignupModal from './signup-modal.vue'
import EventStudent from './event-student.vue'

export default{
	mixins: [ common, globals, wxjssdk ],
	components: {
		SignupModal,
		EventStudent
	},
	data () {
		return {
			data: {},
			branchList: [],
			map_scope: {'class':'班级活动: ','branch':'校区活动: '},
			map_status: {0:'（活动已禁用）',1:'正常',2:'（活动已结束）',3:'（活动已取消）'},
			id: this.$route.params.id
		}
	},
	mounted () {
		this.init()
	},
	computed: {
		EVENTKEY () {
			return 'EVENT-'+this.id
		},
		data_uri () {
			return 'share/event_detail?with=one_class&event_id='+ this.id
		},
		isTimeExpire () {
			return moment(this.data.event_end_time).isBefore(new Date())
		},
		isSignup () {
			let list = this.data.event_sign_up||[]
			if(list.length > 0 && this.sid$) {
				return list.findIndex(l=>l.sid==this.sid$)>-1
			}else{
				let isSignup = Vue.localStorage.get(this.EVENTKEY)					
				return !_.isEmpty(isSignup)
			}
		},
		showSignup () {
			//班级活动  未登录人员禁止报名 
			if(this.data.scope=='class'&&!this.sid$){
				return false
			}
			//活动为非正常状态则禁止报名
			if(this.data.status!=1){
				return false
			}				
			return true
		}
	},
	methods: {
		init () {
			this.init_data()
			this.initBranch()				
		},
		getScope () {
			let result = '全局活动'
			if(this.data.scope!=='global'){
				result = this.map_scope[this.data.scope]
			}
			
			if(this.data.scope=='class'&&this.data.one_class){					
				result += this.data.one_class.class_name
			}
			else if(this.data.scope=='branch'){
				
				this.data.bids.forEach(id=>{
					result += this.$filter('branch_name')(id) + ', '
				})
				result = result.substring(0,result.length - 2)
			}
			
			return result
		},
		init_data () {
			this.$rest(this.data_uri).get()
			.success(data=>{
				this.data = data
				this.setShare()
			}).error(response=>{
				this.toast(response.body.message||'获取数据失败~','warn')
			})
		},
		setShare () {
			let data = this.data
			let [title,img,desc,link] = [data.share_title||data.event_name,data.share_image_url,data.event_image_url,'',data.link_url]
			this.setWxShare(title,img,desc,link)
		},
		initBranch () {
			this.$rest('/sapi/open/global/?name=branchs').get()
			.success(data=>{
				this.branchList = data
				this.$store.commit('updateGlobalVar',{name:'branchs',data:data})
			}).error(response=>{
				this.toast(response.body.message||'获取校区失败~','warn')
			})
		},
		signup () {
			let info = {
				event_id: this.id,
				name: '',
				tel: '',
				bid: 0
			}
			
			if(this.data.apply_nums_limit <= this.data.apply_nums||this.isTimeExpire){
				return false
			}
			
			if(this.sid$ > 0){
				if(this.isSignup){
					return false
				}
				info.sid = this.sid$
				info.bid = this.student$.bid
				this.$rest('event_sign_ups').post(info)
				.success(data=>{
					this.toast('报名成功~','success')
					this.init_data()
				})
			}else{
				this.$refs.info
				.set('info',info)
				.set('scope',this.data.scope)
				.set('bids',this.data.bids)
				.show('请填写报名信息','add')
			}
		}
	}
}



