
import Vue from 'vue'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import modal from '@/libs/modal.mixin'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import PopupPicker from 'c%/PopupPicker.vue'

const emptyObject = {
event_id: 0,
name: '',
tel: '',
bid: 0
}

export default{
mixins: [ common, modal ],
components: {
Group,
XInput,
XButton,
Popup,
PopupPicker
},
data () {
return {
info: util.copy(emptyObject),
scope: 'global',
bids: []
}
},
computed: {
EVENTKEY () {
return 'EVENT-'+this.info.event_id
},
style () {
return {
height: 'auto',
background: '#fff',
padding: '10px',
boxSizing: 'border-box'
}
},
branchList () {
let list = this.gvars('branchs')||[],
bids = this.bids,
result = []
if(bids && bids.length > 0 && list.length > 0){

bids.forEach(b=>{
let index = list.findIndex(l=>l.bid==b)
if(index > -1){
result.push({
key: list[index].bid,
value: list[index].branch_name
})
}
})
}

return result
}
},
methods: {
ok () {
if(!/\s*/.test(this.info.name)){
this.toast('请输入姓名','warn')
return
}
if(this.info.tel.length==0){
this.toast('请输入手机号','warn')
return
}
if(this.info.tel.length<11){
this.toast('手机号格式错误','warn')
return
}

if(this.branchList.length==1){
this.info.bid = this.branchList[0].key
}

if(this.branchList.length > 1&&this.info.bid==0){
this.toast('请选择校区','warn')
return false
}

this.$rest('share/event_sign_up').post(this.info)
.success(data=>{
this.toast('报名成功~','success')
this.$emit('on-success')
Vue.localStorage.set(this.EVENTKEY,true)
this.close()
})
}
}
}



